import App from "./Empresa.svelte";
const initConfig = {
    mainTitle: "Customize your Accora Floor Bed",
    bookADemoHref: "https://us.accora.care/book-a-demo",
};
export const EmpresaConfigurator = (targetId, config, hubspotFormConfig) => {
    const app = new App({
        target: document.getElementById(targetId),
        props: {
            config: Object.assign(Object.assign(Object.assign({}, initConfig), config), { hubspotFormConfig }),
        },
    });
    return app;
};
window.EmpresaConfigurator = EmpresaConfigurator;
export default EmpresaConfigurator;
